import React, { useState } from 'react';
import { Box, Grid, makeStyles, Typography } from '@material-ui/core';
import { TimeInput } from '../../../../components/TimeInput';

const useStyles = makeStyles(theme => ({
	grid: {
		maxWidth: '49%',
	},
	inputWrapper: {
		width: '100%',
		marginTop: theme.spacing(1),
	},
	breakInput: {
		background: theme.palette.common.white,
		padding: theme.spacing(1.5, 2),
	},
}));

type Props = {
	triggerChange: Function;
};

export const BreakTime = ({ triggerChange }: Props) => {
	const classes = useStyles();
	const [breakTime, setBreakTime] = useState({ start: '', end: '' });

	const onChangeBreakTime = (event: React.ChangeEvent<HTMLInputElement>) => {
		const { value, name } = event.target;
		setBreakTime({
			...breakTime,
			[name]: value,
		});
		triggerChange();
	};

	const resetValue = (name: string) => () => {
		setBreakTime({
			...breakTime,
			[name]: '',
		});
	};

	return (
		<Box bgcolor='grey.100' sx={{ p: 2, mb: 2, borderRadius: 0.5 }}>
			<Grid container justifyContent='space-between'>
				<Grid item xs={12} className={classes.grid}>
					<Typography variant='h4'>Break starts at</Typography>
					<TimeInput
						name='start'
						classes={{ root: classes.inputWrapper, input: classes.breakInput }}
						value={breakTime.start}
						onChange={onChangeBreakTime}
						resetValue={resetValue('start')}
					/>
				</Grid>
				<Grid item xs={12} className={classes.grid}>
					<Typography variant='h4'>Break ends at</Typography>
					<TimeInput
						name='end'
						classes={{ root: classes.inputWrapper, input: classes.breakInput }}
						value={breakTime.end}
						onChange={onChangeBreakTime}
						resetValue={resetValue('end')}
					/>
				</Grid>
			</Grid>
		</Box>
	);
};
